@import url(https://fonts.googleapis.com/css?family=Caveat&display=swap);
@import url(https://fonts.googleapis.com/css?family=Anton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#quote-box{height:100vh;width:100vw;display:flex;flex-direction:column;justify-content:center;align-items:center;background-image:-webkit-gradient(linear, left top, left bottom, from(rgba(0,0,0,0.7)), to(rgba(0,0,0,0.7))),url(/random-quote-generator/static/media/ron-swanson.9003ec98.jpg);background-image:linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7)),url(/random-quote-generator/static/media/ron-swanson.9003ec98.jpg);background-position:center;background-size:cover;color:white}#quote-box #text{font-family:"Anton", sans-serif;font-size:1.7em;letter-spacing:2px;max-width:330px}@media screen and (min-width: 800px){#quote-box #text{max-width:700px}}#quote-box #header{font-family:"Caveat", cursive;font-size:3em;margin:0;position:absolute;top:50px}#quote-box #new-quote,#quote-box #tweet-quote{font-weight:600;position:absolute;bottom:50px;font-size:1.2em;padding:0.5em;display:flex;align-items:center;align-content:center}#quote-box #new-quote svg,#quote-box #tweet-quote svg{padding:0.3em}#quote-box #new-quote{margin-left:-90px;background-color:white;border:none;border-radius:4px}#quote-box #tweet-quote{margin-left:110px;color:white}#quote-box #author{position:absolute;top:0;left:0;opacity:0}

